<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 工作流编码 -->
            <a-form-model-item
              label="工作流编码"
              prop="workflowCode"
              ref="workflowCode"
            >
              <a-input
                v-model="entity.workflowCode"
                placeholder="请输入工作流编码"
                name="workflowCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 工作流名称 -->
            <a-form-model-item
              label="工作流名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入工作流名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 工作流类型 -->
            <a-form-model-item
              label="工作流类型"
              prop="workflowType"
              ref="workflowType"
            >
              <a-select
                v-model="entity.workflowType"
                placeholder="请选择工作流类型"
                name="workflowType"
              >
                <a-select-option value="生产工作流">生产工作流</a-select-option>
                <a-select-option value="质检工作流">质检工作流</a-select-option>
                <a-select-option value="采购工作流">采购工作流</a-select-option>
                <a-select-option value="销售工作流">销售工作流</a-select-option>
                <a-select-option value="研发工作流">研发工作流</a-select-option>
                <a-select-option value="服务工作流">服务工作流</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属部门 -->
            <a-form-model-item
              label="所属部门"
              prop="department"
              ref="department"
            >
              <a-select
                v-model="entity.department"
                placeholder="请选择所属部门"
                name="department"
              >
                <a-select-option value="生产部">生产部</a-select-option>
                <a-select-option value="质量部">质量部</a-select-option>
                <a-select-option value="采购部">采购部</a-select-option>
                <a-select-option value="销售部">销售部</a-select-option>
                <a-select-option value="研发部">研发部</a-select-option>
                <a-select-option value="客服部">客服部</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 版本 -->
            <a-form-model-item
              label="版本"
              prop="version"
              ref="version"
            >
              <a-input
                v-model="entity.version"
                placeholder="请输入版本号"
                name="version"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
                name="status"
              >
                <a-select-option value="已发布">已发布</a-select-option>
                <a-select-option value="草稿">草稿</a-select-option>
                <a-select-option value="审核中">审核中</a-select-option>
                <a-select-option value="已废弃">已废弃</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="24">
            <!-- 工作流描述 -->
            <a-form-model-item 
              label="工作流描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入工作流描述"
                :rows="3"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- Workflow Editor Layout -->
      <div class="workflow-editor">
        <!-- Node Palette (Stencil) -->
        <div ref="stencil" class="stencil">
          <div class="stencil-group">
            <h3 class="stencil-group-title">基础节点</h3>
            <div class="stencil-item" @mousedown="startDrag('开始节点', $event)">
              <div class="stencil-icon stencil-start"></div>
              <span>开始</span>
            </div>
            <div class="stencil-item" @mousedown="startDrag('结束节点', $event)">
              <div class="stencil-icon stencil-end"></div>
              <span>结束</span>
            </div>
          </div>
          <div class="stencil-group">
            <h3 class="stencil-group-title">任务节点</h3>
            <div class="stencil-item" @mousedown="startDrag('任务节点', $event)">
              <div class="stencil-icon stencil-task"></div>
              <span>任务</span>
            </div>
            <div class="stencil-item" @mousedown="startDrag('审批节点', $event)">
              <div class="stencil-icon stencil-approval"></div>
              <span>审批</span>
            </div>
          </div>
           <div class="stencil-group">
            <h3 class="stencil-group-title">控制节点</h3>
            <div class="stencil-item" @mousedown="startDrag('分支节点', $event)">
              <div class="stencil-icon stencil-branch"></div>
              <span>分支</span>
            </div>
            <!-- <div class="stencil-item" @mousedown="startDrag('汇合节点', $event)">
              <div class="stencil-icon stencil-merge"></div>
              < span>汇合</span>
            </div> -->
          </div>
      </div>

        <!-- Canvas Area -->
        <div class="canvas-area">
          
          <!-- Graph Canvas -->
          <div class="card canvas-wrapper mb-0">
        <div ref="canvasContainer" class="canvas-container"></div>
          </div>
      </div>

        <!-- Property Panel -->
      <a-drawer
        :title="propertyPanelTitle"
        placement="right"
        :closable="true"
        :visible="isPropertyPanelVisible" 
        @close="closePropertyPanel"
        :width="400"
        :wrap-style="{ overflow: 'auto', paddingBottom: '108px' }"
          :get-container="false" 
          :style="{ position: 'absolute' }"
      >
        <!-- Node Properties Form -->
        <a-form-model 
          v-if="selectedCell && selectedCell.isNode() && selectedCellData" 
          :model="selectedCellData" 
          ref="nodePropertyForm" 
          layout="vertical"
        >
          <a-form-model-item label="节点ID">
            <a-input :value="selectedCellData.id" disabled />
          </a-form-model-item>
          <a-form-model-item label="节点名称" prop="nodeName" :rules="{ required: true, message: '请输入节点名称' }">
            <a-input v-model="selectedCellData.nodeName" @change="updateCellProperties" placeholder="请输入节点名称" />
          </a-form-model-item>
          <a-form-model-item label="节点类型" prop="nodeType">
            <a-select v-model="selectedCellData.nodeType" @change="handleNodeTypeChangeInPanel" placeholder="请选择节点类型" disabled>
                    <a-select-option value="开始节点">开始节点</a-select-option>
                    <a-select-option value="审批节点">审批节点</a-select-option>
                    <a-select-option value="任务节点">任务节点</a-select-option>
                    <a-select-option value="分支节点">分支节点</a-select-option>
                    <a-select-option value="汇合节点">汇合节点</a-select-option>
                    <a-select-option value="结束节点">结束节点</a-select-option>
                  </a-select>
          </a-form-model-item>
          <template v-if="isHandlerRequired(selectedCellData.nodeType)">
            <a-form-model-item label="处理人" prop="handler">
              <a-select v-model="selectedCellData.handler" @change="updateCellProperties" placeholder="请选择处理人">
                    <a-select-option value="部门主管">部门主管</a-select-option>
                    <a-select-option value="项目经理">项目经理</a-select-option>
                    <a-select-option value="质量检验员">质量检验员</a-select-option>
                    <a-select-option value="生产主管">生产主管</a-select-option>
                    <a-select-option value="系统自动">系统自动</a-select-option>
                  </a-select>
            </a-form-model-item>
                </template>
          <template v-if="isTimeLimitRequired(selectedCellData.nodeType)">
             <a-form-model-item label="时限" prop="timeLimit">
               <a-input-number v-model="selectedCellData.timeLimit" :min="0" @change="updateCellProperties" placeholder="请输入时限" style="width: 65%; margin-right: 5%;" />
               <a-select v-model="selectedCellData.timeUnit" @change="updateCellProperties" placeholder="单位" style="width: 30%;">
                    <a-select-option value="小时">小时</a-select-option>
                    <a-select-option value="天">天</a-select-option>
                    <a-select-option value="周">周</a-select-option>
                  </a-select>
             </a-form-model-item>
                </template>
          <template v-if="selectedCellData.nodeType === '分支节点'">
            <a-form-model-item label="分支条件说明" prop="condition">
              <a-textarea v-model="selectedCellData.condition" @change="updateCellProperties" placeholder="请输入条件说明或备注" :rows="3" />
            </a-form-model-item>
                </template>
        </a-form-model>

        <!-- Edge Properties Form -->
         <a-form-model 
          v-if="selectedCell && selectedCell.isEdge() && selectedCellData" 
          :model="selectedCellData" 
          ref="edgePropertyForm" 
          layout="vertical"
        >
           <a-form-model-item label="规则ID">
            <a-input :value="selectedCellData.id" disabled />
          </a-form-model-item>
           <a-form-model-item label="源节点">
            <a-input :value="getNodeNameById(selectedCellData.sourceNode)" disabled />
          </a-form-model-item>
           <a-form-model-item label="目标节点">
            <a-input :value="getNodeNameById(selectedCellData.targetNode)" disabled />
          </a-form-model-item>
           <a-form-model-item label="条件表达式" prop="condition">
             <a-textarea v-model="selectedCellData.condition" @change="updateCellProperties" placeholder="请输入流转条件 (例如: amount > 1000)" :rows="3" />
           </a-form-model-item>
           <a-form-model-item label="优先级" prop="priority">
             <a-input-number v-model="selectedCellData.priority" :min="1" @change="updateCellProperties" placeholder="数字越小优先级越高" style="width: 100%;" />
           </a-form-model-item>
        </a-form-model>

        <a-empty v-if="!selectedCell || !selectedCellData" description="请在画布中选择一个节点或连线" />
        </a-drawer>
        </div>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";
import { Graph, Shape } from '@antv/x6'; // 引入 X6 和 Shape
import { Keyboard } from '@antv/x6-plugin-keyboard' // 引入键盘插件
import { Selection } from '@antv/x6-plugin-selection' // 引入选择插件 (可选，用于多选等)
import { Dnd } from '@antv/x6-plugin-dnd'; // 引入 Dnd 插件
// import { Snapline } from '@antv/x6-plugin-snapline' // 对齐线插件 (如果需要)
// import { History } from '@antv/x6-plugin-history' // 撤销/重做插件 (如果需要)
// import { register } from '@antv/x6-vue-shape'; // 暂时注释

// Helper for unique IDs (simple version)
const generateId = () => `cell_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;

export default {
  name: "CreateOrEditWorkflow",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      graph: null, // X6 Graph 实例
      dnd: null, // Dnd 实例
      selectedCell: null, // 当前选中的 X6 cell (node or edge)
      selectedCellData: null, // 当前选中 cell 对应的业务数据副本
      isPropertyPanelVisible: false, // 控制属性抽屉显示
      entity: {
        id: undefined,
        workflowCode: "",
        name: "",
        workflowType: "",
        department: "",
        version: "",
        description: "",
        nodes: [], // 节点数据结构可能需要调整以存储位置信息 (x, y)
        rules: [], // 规则数据结构保持不变或根据需要调整
        status: "草稿",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      rules: {
        workflowCode: [
          { required: true, message: "请输入工作流编码", trigger: "change" },
          { pattern: /^[A-Z]{2}\d{3}/, message: "工作流编码格式不正确", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入工作流名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "工作流名称不能包含前后空格", trigger: "change" }
        ],
        workflowType: [
          { required: true, message: "请选择工作流类型", trigger: "change" }
        ],
        department: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        version: [
          { required: true, message: "请输入版本号", trigger: "change" },
          { pattern: /^V\d+\.\d+$/, message: "版本号格式不正确，应为Vx.x格式", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ]
      },
      // 移除 nodeColumns 和 ruleColumns
      // nodeColumns: [ ... ], 
      // ruleColumns: [ ... ]
    };
  },

  computed: {
    // 动态计算属性面板标题
    propertyPanelTitle() {
      if (!this.selectedCell) return '属性编辑';
      if (this.selectedCell.isNode()) return `节点属性 (${this.selectedCellData?.nodeName || '未命名'})`;
      if (this.selectedCell.isEdge()) return '规则属性';
      return '属性编辑';
    }
  },

  mounted() {
    this.initGraph(); // 初始化画布
    this.initDnd(); // 初始化 Dnd
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取工作流数据
        setTimeout(() => {
          this.initFormData();
          // 在数据加载后渲染画布内容
          this.$nextTick(() => { // Ensure graph is initialized and container is ready
            if (this.graph) {
              this.loadGraphData(); 
            } else {
              console.error("Graph not initialized before loading data.");
            }
          });
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        // Ensure canvas is cleared for new workflow
        this.$nextTick(() => {
          this.graph?.clearCells();
        });
        this.loading = false;
      }
    },

    // 初始化 X6 画布
    initGraph() {
      const container = this.$refs.canvasContainer;
      if (!container) {
        console.error("Canvas container not found!");
        return;
      }
      
      this.graph = new Graph({
        container: container,
        width: '100%', // Make graph fill container width
        height: '100%', // Make graph fill container height
        grid: { size: 10, visible: true, type: 'dot', args: { color: '#e0e0e0', thickness: 1 } }, // Lighter grid
        panning: true, 
        mousewheel: { enabled: true, zoomAtMousePosition: true, modifiers: 'ctrl', minScale: 0.5, maxScale: 3 },
        connecting: { // 配置连接线规则
          router: 'manhattan', // 智能正交路由
          connector: { // 连接线样式
            name: 'rounded',
            args: {
              radius: 8,
            },
          },
          anchor: 'center',
          connectionPoint: 'anchor',
          allowBlank: false, // 不允许连接到画布空白处
          snap: { // 自动吸附
            radius: 20,
          },
          createEdge: () => { // Use Shape.Edge directly for default edge
             return new Shape.Edge({
               attrs: { line: { stroke: '#8f8f8f', strokeWidth: 1, targetMarker: { name: 'block', width: 12, height: 8 } } },
               zIndex: -1,
             });
          },
          validateConnection({ targetMagnet }) {
            return !!targetMagnet; // 必须连接到连接桩
          },
        },
        highlighting: { // 高亮选项
          magnetAdsorbed: { // 连接桩吸附高亮
            name: 'stroke',
            args: {
              padding: 4,
              attrs: {
                'stroke-width': 2,
                stroke: '#5F95FF',
              },
            },
          },
        },
        selecting: { // 启用选择插件 (配合键盘删除)
          enabled: true,
          rubberband: true, // 启用框选
          showNodeSelectionBox: true, // 显示节点选择框
        },
        keyboard: { // 启用键盘插件
          enabled: true,
        },
        // snapline: true, // 启用对齐线 (需要引入插件)
        // history: true, // 启用历史记录 (需要引入插件)
      });

      // --- 注册插件 ---
      this.graph.use(new Keyboard({ enabled: true }));
      this.graph.use(new Selection({ enabled: true, rubberband: true, showNodeSelectionBox: true }));
      // this.graph.use(new Snapline({ enabled: true }));
      // this.graph.use(new History({ enabled: true }));

      // --- 绑定键盘事件 ---
      this.graph.bindKey('delete', () => {
        const selectedCells = this.graph.getSelectedCells();
        if (selectedCells.length) {
          // removeCells triggers node:removed and edge:removed listeners
          this.graph.removeCells(selectedCells); 
          this.closePropertyPanel(); // Close panel if deleted item was selected
        }
      });
      
      // --- 监听画布事件 ---
      this.setupGraphListeners();

      // --- 调整画布尺寸 ---
      // Optional: Add resize observer if the container size can change dynamically
      const resizeObserver = new ResizeObserver(() => {
        this.graph?.resize(container.clientWidth, container.clientHeight);
      });
      resizeObserver.observe(container);
      this.$once('hook:beforeDestroy', () => {
         resizeObserver.unobserve(container);
      });
    },

    // 初始化 Dnd
    initDnd() {
      if (!this.graph) return;
      this.dnd = new Dnd({
        target: this.graph,
        scaled: false,
        animation: true,
        stencil: this.$refs.stencil, // Optional: Specify stencil container for better integration
        validateNode: (droppingNode, options) => {
          // droppingNode 是从 stencil 拖拽出的节点模型
          // options 包含拖放事件等信息
          const nodeData = droppingNode.getData(); // 获取 startDrag 时设置的数据
          const nodeType = nodeData.type;
          const pos = droppingNode.getPosition(); // 获取画布上的放置坐标

          console.log(`Dropping node type: ${nodeType} at`, pos);

          // --- 创建节点逻辑 (类似之前的 addNodeByType) ---
          const newNodeId = generateId();
          let nodeName = nodeType;
          let defaults = {};

          switch (nodeType) {
            case '开始节点': nodeName = '开始'; break;
            case '结束节点': nodeName = '结束'; break;
            case '任务节点': nodeName = '新任务'; defaults = { handler: '', timeLimit: 8, timeUnit: '小时' }; break;
            case '审批节点': nodeName = '新审批'; defaults = { handler: '', timeLimit: 24, timeUnit: '小时' }; break;
            case '分支节点': nodeName = '条件分支'; defaults = { condition: '' }; break;
            case '汇合节点': nodeName = '并行汇合'; break;
          }

          const newNodeData = {
            id: newNodeId,
            nodeType: nodeType,
            nodeName: nodeName,
            x: Math.round(pos.x), // 使用拖放的坐标
            y: Math.round(pos.y),
            sequence: (this.entity.nodes.length + 1) * 10, 
            ...defaults
          };

          // 1. 更新 entity 数据
          this.entity.nodes.push(newNodeData);
          console.log('Node added to entity:', newNodeData);

          // 2. 创建 X6 节点配置 (使用 helper 函数)
          const nodeConfig = this.createNodeConfig(newNodeData); 
          
          // 3. 返回 X6 节点配置，Dnd 插件会自动添加到画布
          return this.graph.createNode(nodeConfig); 
        },
      });
    },

    // 开始拖拽 (由 stencil item 的 mousedown 触发)
    startDrag(nodeType, e) {
      if (!this.graph || !this.dnd) return;

      // 创建一个临时的、符合 X6 节点结构的对象，用于拖拽过程的预览
      // 这个节点的配置应该与最终添加到画布上的节点视觉效果一致
      const tempNodeData = { type: nodeType }; // 存储节点类型
      const tempNodeConfig = this.createNodeConfig({ 
        id: 'temp', // 临时 ID
        nodeType: nodeType, 
        nodeName: nodeType, // 拖拽时显示的文字
        x: 0, y: 0 // 初始位置不重要
      });

      if (!tempNodeConfig) return;

      const node = this.graph.createNode({
        ...tempNodeConfig,
        data: tempNodeData, // 将节点类型存入 data
      });

      // 启动 Dnd 拖拽
      this.dnd.start(node, e); 
    },

    // 设置画布事件监听
    setupGraphListeners() {
      if (!this.graph) return;

      // 节点移动
      this.graph.on('node:moved', ({ node }) => {
        const nodeId = node.id;
        const position = node.getPosition();
        const entityNode = this.entity.nodes.find(n => n.id === nodeId);
        if (entityNode) {
          // Use $set for potential reactivity issues, though direct might work
          this.$set(entityNode, 'x', position.x);
          this.$set(entityNode, 'y', position.y);
          console.log('Node moved:', nodeId, position);
        }
      });

      // 连接线创建 (用户操作)
      this.graph.on('edge:connected', ({ edge, isNew }) => {
        if (isNew) { // Only handle edges created by user interaction
          const sourceNodeId = edge.getSourceCellId();
          const targetNodeId = edge.getTargetCellId();
          
          // Check if rule already exists (e.g., due to rapid clicks)
          const exists = this.entity.rules.some(r => r.id === edge.id);
          if (!exists && sourceNodeId && targetNodeId) {
            const newRule = {
              id: edge.id, // Use X6 generated ID
              sourceNode: sourceNodeId,
              targetNode: targetNodeId,
              condition: "", // Default condition
              priority: 1,   // Default priority
              // Add other rule properties if needed
            };
            this.entity.rules.push(newRule);
            edge.setData(newRule); // Store rule data in the edge
            console.log('Rule added:', newRule);
          }
        }
      });

      // 节点/边删除 (监听键盘 Delete 或后续通过 API 删除)
      // Note: X6 doesn't have a built-in 'cell:removed' event that distinguishes user vs API.
      // We'll handle removal when we implement delete functionality (e.g., button or keyboard).
      // For now, let's add listeners for API removal for completeness.
      this.graph.on('node:removed', ({ node }) => {
        const nodeId = node.id;
        const nodeIndex = this.entity.nodes.findIndex(n => n.id === nodeId);
        if (nodeIndex !== -1) {
          this.entity.nodes.splice(nodeIndex, 1);
          console.log('Node removed from entity:', nodeId);
          
          // Remove associated rules
          this.entity.rules = this.entity.rules.filter(rule => 
            rule.sourceNode !== nodeId && rule.targetNode !== nodeId
          );
          console.log('Associated rules removed for node:', nodeId);
        }
      });

      this.graph.on('edge:removed', ({ edge }) => {
        const ruleId = edge.id;
        const ruleIndex = this.entity.rules.findIndex(r => r.id === ruleId);
        if (ruleIndex !== -1) {
          this.entity.rules.splice(ruleIndex, 1);
          console.log('Rule removed from entity:', ruleId);
        }
      });

      // 点击事件 (用于后续属性面板)
      this.graph.on('cell:click', ({ cell }) => {
        console.log('Cell clicked:', cell.id, cell.isNode() ? 'Node' : 'Edge');
        
        // Reset visual styles manually if not using Selection plugin styling
        this.graph.getNodes().forEach(node => node.removeTools());
        this.graph.getEdges().forEach(edge => edge.attr('line/stroke', '#8f8f8f'));

        if (cell.isNode()) {
          cell.addTools([{ name: 'boundary', args: { attrs: { fill: 'none', stroke: '#5F95FF', 'stroke-width': 1 } } }]);
          this.selectedCell = cell;
          // Create a deep copy for editing to avoid modifying original data directly
          this.selectedCellData = JSON.parse(JSON.stringify(cell.getData() || {})); 
          this.isPropertyPanelVisible = true;
        } else if (cell.isEdge()) {
          cell.attr('line/stroke', '#5F95FF');
          this.selectedCell = cell;
          this.selectedCellData = JSON.parse(JSON.stringify(cell.getData() || {}));
          this.isPropertyPanelVisible = true;
        } else {
           this.closePropertyPanel(); // Should not happen if clicking a cell
        }
      });

      this.graph.on('blank:click', () => {
        console.log('Blank clicked');
        // this.graph.cleanSelection(); // Clear plugin selection
        // Manually clear styles
        this.graph.getNodes().forEach(node => node.removeTools());
        this.graph.getEdges().forEach(edge => edge.attr('line/stroke', '#8f8f8f'));
        this.closePropertyPanel();
      });

      // Listen for changes in cell data (e.g., label changes) to potentially update panel
      this.graph.on('cell:change:data', ({ cell }) => {
        if (this.selectedCell && this.selectedCell.id === cell.id) {
          // If the currently selected cell's data changes externally, update the panel
          this.selectedCellData = JSON.parse(JSON.stringify(cell.getData() || {}));
          console.log('Selected cell data changed externally, updating panel.');
        }
      });
       // Listen for label changes specifically
      this.graph.on('cell:change:attrs', ({ cell, key, current }) => {
        if (this.selectedCell && this.selectedCell.id === cell.id && key === 'label/text') {
           if (this.selectedCellData && this.selectedCellData.nodeName !== current) {
             this.$set(this.selectedCellData, 'nodeName', current);
             console.log('Selected cell label changed externally, updating panel data.');
           }
        }
      });
    },
    
    // 加载数据到画布 - 实现步骤 2
    loadGraphData() {
      if (!this.graph) return;

      console.log("Loading graph data:", this.entity.nodes, this.entity.rules);
      
      const nodes = this.entity.nodes.map(node => {
        let shape = 'rect'; // Default shape
        let width = 120;
        let height = 50;
        let fillColor = '#EFF4FF'; // Default fill
        let strokeColor = '#5F95FF'; // Default stroke

        // Customize appearance based on nodeType
        switch (node.nodeType) {
          case '开始节点':
            shape = 'circle';
            width = 60;
            height = 60;
            fillColor = '#95de64'; // Green
            strokeColor = '#52c41a';
            break;
          case '结束节点':
            shape = 'circle';
            width = 60;
            height = 60;
            fillColor = '#ffccc7'; // Red
            strokeColor = '#f5222d';
            break;
          case '审批节点':
            fillColor = '#fff1b8'; // Yellow
            strokeColor = '#faad14';
            break;
          case '任务节点':
            fillColor = '#e6f7ff'; // Light blue
            strokeColor = '#1890ff';
            break;
          case '分支节点':
            shape = 'polygon'; // Diamond shape
            width = 80;
            height = 80;
            fillColor = '#d3adf7'; // Purple
            strokeColor = '#722ed1';
            break;
          case '汇合节点':
             shape = 'polygon'; // Diamond shape
            width = 80;
            height = 80;
            fillColor = '#d3adf7'; // Purple
            strokeColor = '#722ed1';
            break;
          // Add more types if needed
        }

        return {
          id: node.id,
          shape: shape,
          x: node.x || 100, // Use stored or default position
          y: node.y || 100,
          width: width,
          height: height,
          label: node.nodeName, // Display node name
          data: node, // Store original node data
          attrs: { // Basic styling
            body: {
              fill: fillColor,
              stroke: strokeColor,
              strokeWidth: 1,
              rx: shape === 'rect' ? 4 : 0, // Rounded corners for rect
              ry: shape === 'rect' ? 4 : 0,
            },
            label: {
              text: node.nodeName,
              fill: '#333',
              fontSize: 12,
            },
            // Specific attrs for polygon (diamond)
            ...(shape === 'polygon' && {
              body: {
                refPoints: '0,10 10,0 20,10 10,20', // Diamond points (adjust scale if needed)
                fill: fillColor,
                stroke: strokeColor,
                strokeWidth: 1,
              },
              label: { // Adjust label position for diamond
                refY: 0.5, 
                yAdjust: 0,
              }
            }),
          },
          ports: { // Add connection ports (optional but recommended)
            groups: {
              top: { position: 'top', attrs: { circle: { r: 4, magnet: true, stroke: '#5F95FF', strokeWidth: 1, fill: '#fff' } } },
              bottom: { position: 'bottom', attrs: { circle: { r: 4, magnet: true, stroke: '#5F95FF', strokeWidth: 1, fill: '#fff' } } },
              left: { position: 'left', attrs: { circle: { r: 4, magnet: true, stroke: '#5F95FF', strokeWidth: 1, fill: '#fff' } } },
              right: { position: 'right', attrs: { circle: { r: 4, magnet: true, stroke: '#5F95FF', strokeWidth: 1, fill: '#fff' } } },
            },
            items: [
              { id: 'port_top', group: 'top' },
              { id: 'port_bottom', group: 'bottom' },
              { id: 'port_left', group: 'left' },
              { id: 'port_right', group: 'right' },
            ]
          }
        };
      });

      const edges = this.entity.rules.map(rule => ({
        id: rule.id,
        shape: 'edge', // Use the default edge shape configured in initGraph
        source: { cell: rule.sourceNode, port: 'port_bottom' }, // Example: connect from bottom port
        target: { cell: rule.targetNode, port: 'port_top' },   // Example: connect to top port (adjust logic as needed)
        label: rule.condition || '', // Display condition as label (optional)
        data: rule, // Store original rule data
        attrs: { // Can override default edge attrs here if needed
          line: {
            stroke: '#8f8f8f',
            strokeWidth: 1,
            targetMarker: { name: 'block', width: 12, height: 8 },
          },
        },
        zIndex: -1, // Ensure edges are behind nodes
      }));

      // Use fromJSON to batch load nodes and edges
      this.graph.fromJSON({ nodes, edges });
      this.graph.centerContent(); // Center the content after loading
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        workflowCode: "",
        name: "",
        workflowType: "",
        department: "",
        version: "",
        description: "",
        nodes: [],
        rules: [],
        status: "草稿",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
      // Clear the canvas when resetting the form
      this.graph?.clearCells(); 
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        // --- Existing Logic: Load from provided row data ---
        const row = this.ndoEditParams.row;
        // Ensure nodes have x, y. Use ?? for nullish coalescing.
        const nodesWithPosition = (row.nodes || []).map((node, index) => ({ 
          ...node, 
          // Provide default positions if missing, with some spread
          x: node.x ?? 100 + (index % 5) * 180, // Increased spread
          y: node.y ?? 100 + Math.floor(index / 5) * 120 
        }));
        
        this.entity = {
          ...this.entity, // Keep existing defaults like status
          ...row,
          nodes: nodesWithPosition,
          rules: row.rules || []
        };
        console.log("Loaded data from ndoEditParams.row:", this.entity);

      } else {
        // --- New Logic: Generate Mock Data for Canvas ---
        console.log("Generating mock data for new workflow.");
        const mockNodes = [
          { id: 'node_start', nodeType: '开始节点', nodeName: '开始', x: 100, y: 100, sequence: 10 },
          { id: 'node_task1', nodeType: '任务节点', nodeName: '数据准备', handler: '生产主管', timeLimit: 8, timeUnit: '小时', x: 300, y: 100, sequence: 20 },
          { id: 'node_approval1', nodeType: '审批节点', nodeName: '主管审批', handler: '部门主管', timeLimit: 24, timeUnit: '小时', x: 500, y: 100, sequence: 30 },
          { id: 'node_branch', nodeType: '分支节点', nodeName: '条件判断', x: 700, y: 100, sequence: 40 },
          { id: 'node_task2_a', nodeType: '任务节点', nodeName: '特殊处理A', handler: '项目经理', timeLimit: 16, timeUnit: '小时', x: 900, y: 50, sequence: 50 },
          { id: 'node_task2_b', nodeType: '任务节点', nodeName: '常规处理B', handler: '质量检验员', timeLimit: 8, timeUnit: '小时', x: 900, y: 150, sequence: 51 }, // Note sequence for potential ordering
          { id: 'node_merge', nodeType: '汇合节点', nodeName: '结果汇合', x: 1100, y: 100, sequence: 60 },
          { id: 'node_end', nodeType: '结束节点', nodeName: '结束', x: 1300, y: 100, sequence: 70 },
        ];

        const mockRules = [
          { id: 'rule_1', sourceNode: 'node_start', targetNode: 'node_task1', priority: 1 },
          { id: 'rule_2', sourceNode: 'node_task1', targetNode: 'node_approval1', priority: 1 },
          { id: 'rule_3', sourceNode: 'node_approval1', targetNode: 'node_branch', priority: 1 },
          { id: 'rule_4a', sourceNode: 'node_branch', targetNode: 'node_task2_a', condition: 'value > 100', priority: 1 },
          { id: 'rule_4b', sourceNode: 'node_branch', targetNode: 'node_task2_b', condition: 'value <= 100', priority: 2 }, // Lower priority
          { id: 'rule_5a', sourceNode: 'node_task2_a', targetNode: 'node_merge', priority: 1 },
          { id: 'rule_5b', sourceNode: 'node_task2_b', targetNode: 'node_merge', priority: 1 },
          { id: 'rule_6', sourceNode: 'node_merge', targetNode: 'node_end', priority: 1 },
        ];

        // Assign mock data to the entity, keeping other fields default or empty
        this.entity = {
          ...this.entity, // Keep existing defaults like status, workflowCode etc.
          id: undefined, // Ensure ID is undefined for new
          nodes: mockNodes,
          rules: mockRules,
          // Reset metadata for new workflow
          createdBy: "", 
          createdTime: "",
          modifiedBy: "",
          modifiedTime: "",
        };
         console.log("Applied mock data:", this.entity);
      }
    },
    
    async save() {
      // 保存校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        
        // Positions should be up-to-date via 'node:moved' listener.
        // No explicit update needed here unless 'node:moved' is unreliable.
        // console.log("Saving entity:", JSON.stringify(this.entity, null, 2)); 

        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // Helper to create node config for rendering (extracted from loadGraphData)
    createNodeConfig(node) {
        let shape = 'rect'; let width = 120; let height = 50;
        let fillColor = '#EFF4FF'; let strokeColor = '#5F95FF';

        switch (node.nodeType) {
          case '开始节点': shape = 'circle'; width = 60; height = 60; fillColor = '#95de64'; strokeColor = '#52c41a'; break;
          case '结束节点': shape = 'circle'; width = 60; height = 60; fillColor = '#ffccc7'; strokeColor = '#f5222d'; break;
          case '审批节点': fillColor = '#fff1b8'; strokeColor = '#faad14'; break;
          case '任务节点': fillColor = '#e6f7ff'; strokeColor = '#1890ff'; break;
          case '分支节点': shape = 'polygon'; width = 80; height = 80; fillColor = '#d3adf7'; strokeColor = '#722ed1'; break;
          case '汇合节点': shape = 'polygon'; width = 80; height = 80; fillColor = '#d3adf7'; strokeColor = '#722ed1'; break;
        }

        return {
          id: node.id, shape: shape, x: node.x, y: node.y, width: width, height: height,
          label: node.nodeName, data: node, // IMPORTANT: Store data again
          attrs: { 
            body: { fill: fillColor, stroke: strokeColor, strokeWidth: 1, rx: shape === 'rect' ? 4 : 0, ry: shape === 'rect' ? 4 : 0 },
            label: { text: node.nodeName, fill: '#333', fontSize: 12 },
            ...(shape === 'polygon' && {
              body: { refPoints: '0,10 10,0 20,10 10,20', fill: fillColor, stroke: strokeColor, strokeWidth: 1 },
              label: { refY: 0.5, yAdjust: 0 }
            }),
          },
          ports: { /* ... ports config from loadGraphData ... */ 
             groups: {
              top: { position: 'top', attrs: { circle: { r: 4, magnet: true, stroke: '#5F95FF', strokeWidth: 1, fill: '#fff' } } },
              bottom: { position: 'bottom', attrs: { circle: { r: 4, magnet: true, stroke: '#5F95FF', strokeWidth: 1, fill: '#fff' } } },
              left: { position: 'left', attrs: { circle: { r: 4, magnet: true, stroke: '#5F95FF', strokeWidth: 1, fill: '#fff' } } },
              right: { position: 'right', attrs: { circle: { r: 4, magnet: true, stroke: '#5F95FF', strokeWidth: 1, fill: '#fff' } } },
            },
            items: [ { id: 'port_top', group: 'top' }, { id: 'port_bottom', group: 'bottom' }, { id: 'port_left', group: 'left' }, { id: 'port_right', group: 'right' } ]
          }
        };
    },

    // --- Property Panel Methods ---
    closePropertyPanel() {
      this.isPropertyPanelVisible = false;
      this.selectedCell = null;
      this.selectedCellData = null;
      // Optionally clear graph selection visuals if not handled by blank:click
      // this.graph?.cleanSelection(); 
      // this.graph?.getNodes().forEach(node => node.removeTools());
      // this.graph?.getEdges().forEach(edge => edge.attr('line/stroke', '#8f8f8f'));
    },

    // Called when any property changes in the drawer's form
    updateCellProperties() {
       this.$nextTick(() => { // Ensure data is updated before processing
        if (!this.selectedCell || !this.selectedCellData) return;

        const id = this.selectedCellData.id;
        let targetEntity = null;
        let entityIndex = -1;

        if (this.selectedCell.isNode()) {
          entityIndex = this.entity.nodes.findIndex(n => n.id === id);
          if (entityIndex !== -1) {
            targetEntity = this.entity.nodes[entityIndex];
            // Update node label on graph
            if (targetEntity.nodeName !== this.selectedCellData.nodeName) {
               this.selectedCell.setLabel(this.selectedCellData.nodeName || ''); 
            }
          }
        } else if (this.selectedCell.isEdge()) {
          entityIndex = this.entity.rules.findIndex(r => r.id === id);
           if (entityIndex !== -1) {
            targetEntity = this.entity.rules[entityIndex];
             // Update edge label (condition) on graph
            if (targetEntity.condition !== this.selectedCellData.condition) {
               this.selectedCell.setLabels(this.selectedCellData.condition || '');
            }
          }
        }

        if (targetEntity) {
          // Update the actual entity data using Object.assign for reactivity
          Object.assign(targetEntity, this.selectedCellData);
          
          // Update the data stored within the X6 cell itself
          this.selectedCell.setData(this.selectedCellData); 

          console.log('Updated entity data:', targetEntity);
        } else {
          console.warn("Could not find corresponding entity to update for cell:", id);
        }
      });
    },
    
    // Handle node type change specifically (if enabled in panel)
    handleNodeTypeChangeInPanel() {
       if (!this.selectedCell || !this.selectedCellData || !this.selectedCell.isNode()) return;
       
       console.log("Node type changed in panel to:", this.selectedCellData.nodeType);
       // 1. Update visual appearance (shape, color, size)
       const nodeConfig = this.createNodeConfig(this.selectedCellData);
       if (nodeConfig) {
         this.selectedCell.prop(nodeConfig); // Update props like shape, size
         this.selectedCell.attr(nodeConfig.attrs); // Update attributes like color, label text
       }
       
       // 2. Reset/clear fields that are no longer relevant
       if (!this.isHandlerRequired(this.selectedCellData.nodeType)) {
         this.selectedCellData.handler = undefined; 
       }
       if (!this.isTimeLimitRequired(this.selectedCellData.nodeType)) {
         this.selectedCellData.timeLimit = undefined;
         this.selectedCellData.timeUnit = undefined;
       }
       if (this.selectedCellData.nodeType !== '分支节点') {
          // Keep condition for edges, maybe clear node condition? Let's keep it for now.
          // this.selectedCellData.condition = undefined; 
       }

       // 3. Trigger general update
       this.updateCellProperties();
    },

    // Helper to get node name by ID (for edge panel)
    getNodeNameById(nodeId) {
      const node = this.entity.nodes.find(n => n.id === nodeId);
      return node ? node.nodeName : '未知节点';
    },

    // Helpers from original table logic (can be reused)
    isHandlerRequired(nodeType) {
      return nodeType === '审批节点' || nodeType === '任务节点';
    },
    isTimeLimitRequired(nodeType) {
      return nodeType === '审批节点' || nodeType === '任务节点';
    },
  },

  // 组件销毁前清理画布和 Dnd
  beforeDestroy() {
    this.graph?.dispose();
    // Dnd 实例不需要手动 dispose
  },
};
</script>

